import React from "react";
import { Carousel } from "react-responsive-carousel";

class ARG extends React.Component{

  constructor(){
		super();
		this.state={
      startTouch:[],
      endTouch:[],
      countTouchDown:0,
      countTouchUp:0,
      swipeable_:true,
      variabileContatore:0,
		}
	}
  componentDidMount(){


    window.addEventListener("touchstart", (e) => {
      if(e.touches.length>=2){
        this.setState((prevState) => {
				return {variabileContatore:2};
        });
        this.setState({startTouch:e.touches},()=>{
          //console.log(this.state.startTouch)
        })
        this.setState({swipeable_:false})
      }
    },true)
    window.addEventListener("touchend", (e) => {
    if(this.state.variabileContatore>0)
    {  this.state.endTouch.push(e.changedTouches["0"])

      if(
      (this.state.endTouch[this.state.endTouch.length-1]!==undefined)
      &&
      (this.state.startTouch["0"]!==undefined)
      &&
      (this.state.endTouch.length%2===0)
      )
      {



          let uno1 = Math.abs(this.state.startTouch["0"].clientX-this.state.startTouch["1"].clientX)
          let due2 = Math.abs(this.state.startTouch["0"].clientY-this.state.startTouch["1"].clientY)
          let lunghezzaPrimoPinch = Math.sqrt(Math.pow(uno1, 2)+Math.pow(due2, 2))
          let tre3 = Math.abs(this.state.endTouch[this.state.endTouch.length-1].clientX-this.state.endTouch[this.state.endTouch.length-2].clientX)
          let quattro4 = Math.abs(this.state.endTouch[this.state.endTouch.length-1].clientY-this.state.endTouch[this.state.endTouch.length-2].clientY)
          let lunghezzaSecondoPinch = Math.sqrt(Math.pow(tre3, 2)+Math.pow(quattro4, 2))

          if(lunghezzaPrimoPinch===lunghezzaSecondoPinch){
          }else if (lunghezzaPrimoPinch<lunghezzaSecondoPinch) {
            this.setState({swipeable_:false})
          }else{
          this.setState({swipeable_:true})
          }



      }
      this.setState((prevState) => {
      return {variabileContatore:prevState.variabileContatore-1};
      });
    }

    },true)


  }

  render(){

    function changeCurrentId(e){
      gino.updateCurrentId(imgArr[e].key)
    }

    let gino = this.props;

    const arrayRobotsAsc = gino.robots;
  	const arrayRobotsDesc = [];



    let lunghezzaCarousel = gino.inWidth;
    parseInt(lunghezzaCarousel, 10);
    lunghezzaCarousel = lunghezzaCarousel*0.9;
    lunghezzaCarousel = lunghezzaCarousel + "px";

    let altezzaCarousel = gino.inHeight;
    parseInt(altezzaCarousel, 10);
    altezzaCarousel = altezzaCarousel*0.9;

    let numSalvato = 0;


    const imgStyle={
      maxHeight: `${altezzaCarousel}px`,
      width: 'auto',
    }

    

    let imgArr = [];
    let imgArr3 = [];

  	for (var i = arrayRobotsAsc.length - 1; i >= 0; i--) {

      // eslint-disable-next-line
      let linkMOD ="";
    	const lInk = arrayRobotsAsc[i].link;
    	var pos = lInk.lastIndexOf("/");
    	let oKGIF = true;
    	const lInkpart2 = lInk.slice(pos, lInk.length);
    	const lInkpart3 = lInkpart2.slice((lInkpart2.length-3), lInkpart2.length);
    	if(lInkpart3==='gif')
    		oKGIF= false;

    	let lInkMOD = lInk;
    		if(gino.type!=="mobile"){
    			//console.log("desk///////////////")

    		}else if(oKGIF){
    			//console.log("mobile///////////////")
    			const lInkpart1Mobile = lInk.slice(0, pos);
    			const lInkpart2Mobile = lInk.slice(pos, lInk.length);
    			lInkMOD = lInkpart1Mobile + "/okMobile" + lInkpart2Mobile;
    			if(lInkMOD==="/okMobile")
    				linkMOD = "";
    			else
    				linkMOD = lInkMOD;
    		}


      //
      imgArr.push(<img className="imgCarousel" alt={arrayRobotsAsc[i].id} id={arrayRobotsAsc[i].id} key={arrayRobotsAsc[i].id} style={imgStyle} src={lInkMOD} />)
  		arrayRobotsDesc[arrayRobotsAsc.length-1-i]=arrayRobotsAsc[i];
  	}

    let immagini = document.getElementsByClassName("card");


    for(var counter=0; counter<immagini.length; counter++){
      if(/\d/.test(immagini.item(counter).id)){


        for(let counter2=0; counter2<imgArr.length; counter2++){
          if(Number(immagini.item(counter).alt)===Number(imgArr[counter2].key))
            imgArr3.push(imgArr[counter2])//immagini.item(counter)

        }
      }
    }

    if(imgArr3.length!==0){


      for (var i2 = imgArr.length - 1; i2 >= 0; i2--) {
        //console.log(gino.currentImg)
      if(imgArr[i2].key===gino.currentImg)
    		  numSalvato = i2;
    	}









  return(
    <Carousel
    useKeyboardArrows={true}
    emulateTouch={true}
    showStatus={false}
    showIndicators={false}
    showThumbs={false}
    swipeable={this.state.swipeable_}
    className="carouselMio"
    //dynamicHeight={true}
    selectedItem={numSalvato}
    //onClickItem={swipeToRight}
    onChange={(e)=>{changeCurrentId(e)}}
    width={lunghezzaCarousel}
    >

      {imgArr3}



    </Carousel>
  );
  }
  else
  {return(<div id="niente di niente"></div>);}

  }

}


export default ARG;



